<template>
   <div class="login">
     <h1>{{welcome}}</h1>
     <h2>{{welcomeSub}}</h2>
     <ul>
       <li>
         <h3 :style="warning1">{{account}}</h3>
         <input :style="warning3" v-model="aValue" @keyup.enter='loging' spellcheck ='false' type="text">
       </li>
       <li>
         <h3 :style="warning2">{{password}}</h3>
         <input :style="warning4" v-model="pValue" @keyup.enter='loging' type="password">
       </li>
       <li>
         <h3>{{forgot}}</h3>
         <button @click='loging' >{{login}}</button>
       </li>
     </ul>
   </div>
</template>

<style lang="scss" scoped>
.login {
  @include flex-column(center);
  position: fixed;
  top: 20%;
  left: 40%;
  @include size(75vh, 66vh);
  border: 1px solid $body-black;
  box-shadow: 0 1px 6px $box-shadow;
  border-radius: 10px;
  background-color: $box;
  transform: translateX(-20%);
  // transition: all 0.2s ease-in-out;
  h2 {
    color: $box-title-small;
    font-weight: normal;
  }
  ul {
    @include flex-column(flex-end);
    height: 66%;
    width: 80%;
    li {
      @include size(100%, 33%);
      color: $box-input-title;
      h3 {
        font-size: 1rem;
        text-align: left;
        font-weight: normal;
        padding-bottom: 5px;
      }
      input {
        width: 100%;
        padding: 0.8rem;
        color: $box-title;
        border: 1px solid $body-black;
        border-radius: 3px;
        background-color: $box-input;
        font-size: 1.3rem;
        &:hover {
          border-color: $box-input-hover;
        }
      }
      button {
        color: $box-title;
        background-color: $box-botton;
        border-radius: 3px;
        border: none;
        @include size(100%, auto);
        padding: 0.8rem;
        font-size: 1.5rem;
        font-weight: bold;
        margin: 10px 0;
        &:hover {
          background-color: $box-botton-hover;
        }
      }
      &:last-child {
        color: $box-botton;
      }
    }
  }
  @include respond-to(medium) {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @include size(100vw, 100vh);
    transform: none;
  }
}
</style>

<script>
export default {
  name: 'LoginPanel',
  data: function() {
    return {
      welcome: 'おかえりなさい!',
      welcomeSub: 'またお会いしましたね!',
      account: '社員コード',
      aValue: '',
      password: 'パスワード',
      pValue: '',
      forgot: 'Forgot your password?',
      login: 'ログイン',
      warning1: {},
      warning2: {},
      warning3: {},
      warning4: {}
    }
  },
  mounted: function() {
    this.$parent.$parent.$el.className += 'spec'
  },
  destroyed: function() {
    this.$parent.$parent.$el.className = ''
  },
  methods: {
    async loging() {
      const that = this
      const warnColor = 'rgb(143,119,181)'
      // 邮箱验证正则
      // const test = /^([a-zA-Z0-9_-]+(\.)*)+@([a-zA-Z0-9_-]+\.)+[a-zA-Z0-9_-]{2,3}$/

      // 这里是输入验证的颜色还原部分
      that.account = '社員コード'
      that.forgot = 'Forgot your password?'
      that.warning1.color = 'rgb(104,109,116)'
      that.warning2.color = 'rgb(104,109,116)'
      that.warning3.borderColor = 'rgb(43,43,47)'
      that.warning4.borderColor = 'rgb(43,43,47)'

      // 网络api，axios封装在api文件夹内, 采用了模拟的数据
      const res = await that.api.login()
      const _res = res.data.user
      const response = _res.find(e => e.name === that.aValue)
      if (response) {
        if (response.password === that.pValue) {
          return that.$router.push('/home')
        }
        that.forgot = 'パスワード、或は社員コードが入力不正になりました。'
        that.warning3.borderColor = warnColor
        that.warning4.borderColor = warnColor
      } else {
        that.account = '社員コードがまだ登録しませんでした。'
        that.warning1.color = warnColor
        that.warning3.borderColor = warnColor
      }
    }
  }
}
</script>
